$barColor: #f04;
$overlayColor: #222;
$backColor: #fff;
$size: 100px;

.progress-radial {
    position: relative;
    width: $size;
    height: $size;
    border-radius: 50%;
    background-color: $barColor;
}

.progress-radial .overlay {
    position: absolute;
    color: $barColor;
    width: $size - 30;
    height: $size - 30;
    background-color: $overlayColor;
    border-radius: 50%;
    margin-left: 15px;
    margin-top: 15px;
    text-align: center;
    line-height: $size - 30;
    font-size: 24px;
}

$step: 1;
$loops: round(100 / $step);
$increment: 360 / $loops;
$half: round($loops / 2);

@for $i from 0 through $loops {
    .progress-#{$i*$step} {
        @if $i < $half {
            $nextdeg: 90deg + ( $increment * $i );
            background-image: linear-gradient(90deg, $backColor 50%, transparent 50%, transparent), linear-gradient($nextdeg, $barColor 50%, $backColor 50%, $backColor);
        } @else {
            $nextdeg: -90deg + ( $increment * ( $i - $half ) );
            background-image: linear-gradient($nextdeg, $barColor 50%, transparent 50%, transparent), linear-gradient(270deg, $barColor 50%, $backColor 50%, $backColor);
        }
    }
}

.mh-override {
    display: block !important;
}

button, input, optgroup, select, textarea {
    font-family: "Exo 2", sans-serif !important;
}


@import 'custom/style.loader';
@import 'custom/style.normal';

// small screens
@media only screen and (max-width: 349px) {}

// portrait - phones
@media only screen and (max-width: 543px) {

    @import 'custom/style.phone';
}

// landscape - phones
@media only screen and (min-width: 544px) {

    @import 'custom/style.tablets';
}

// medium - tablets
@media only screen and (min-width: 768px) {
    .modal {
        text-align: center;
        padding: 0!important;
        &:before {
            content: '';
            display: inline-block;
            height: 100%;
            vertical-align: middle;
            margin-right: -4px;
        }
    }

    .modal-dialog {
        display: inline-block;
        text-align: left;
        vertical-align: middle;
    }
    @import 'custom/style.laptop';
}

// large - laptops
@media only screen and (min-width: 992px) {

    @import 'custom/style.large';
}

// Extra large - desktops
@media only screen and (min-width: 1200px) {

    @import 'custom/style.xlarge';
}
